<template>
  <!-- 上 orderList存在 -->
  <section class="row" v-if="cookieOrderList.length > 0">
    <ExistOrder :cookieOrderList="cookieOrderList" />
  </section>
  <!-- 上 orderList不存在 -->
  <section class="row" v-else>
    <Unorder />
  </section>
  <!-- 下 -->
  <section class="row">
    <div class="card border-dark paperDarkBg">
      <div class="card-body d-flex align-items-start text-uppercase">
        <span class="h4 me-3">step 2</span
        ><span class="h3" style="font-family: BellGothicStd"
          >Thank you for coming</span
        >
      </div>
    </div>
    <div class="card border-dark">
      <div class="d-flex justify-content-center">
        <img src="@/assets/images/thanks.jpg" alt="" class="w-sm-50" />
      </div>
    </div>
  </section>
</template>

<script>
import Cookies from 'js-cookie';
import ExistOrder from '@/components/order/OrderExist.vue';
import Unorder from '@/components/order/OrderUnExist.vue';

export default {
  data() {
    return {
      cookieOrderList: [],
    };
  },
  components: {
    ExistOrder,
    Unorder,
  },
  methods: {
    getCookieOrder() {
      this.cookieOrderList = [];
      if (Cookies.get('luluBakeOrder')) {
        const tempCookie = Cookies.get('luluBakeOrder').split(';');
        Object.values(tempCookie).forEach((item) => {
          this.cookieOrderList.push(JSON.parse(item));
        });
        this.cookieOrderList.reverse();
      }
    },
  },
  mounted() {
    this.getCookieOrder();
  },
};
</script>
