<!-- eslint-disable max-len -->
<template>
  <div class="card border-dark paperDarkBg">
    <div class="card-body d-flex align-items-start text-uppercase">
      <span class="h4 me-3">step 1</span
      ><span class="h3" style="font-family: BellGothicStd">Your Oder</span>
    </div>
  </div>
  <div class="col-md-4 border border-dark">
    <div class="list-group" id="list-tab" role="tablist">
      <template v-for="(item, index) in cookieOrderList" :key="index">
        <router-link
          :to="`/order/${item.cookieId}`"
          class="fw-bold my-2 d-flex justify-content-between align-items-center text-decoration-none"
          @click="getOrder(item.orderId)"
        >
          <span class="position-relative"
            >{{ item.cookieId }}
            <CircleThree
              :class="{
                'd-none':
                  this.$route.path.split('/')[2] !== item.cookieId.toString(),
              }" /></span
          ><span class="badge text-bg-primary rounded-pill">{{
            item.num
          }}</span>
        </router-link>
      </template>
    </div>
  </div>
  <div class="col-md-8 border border-dark">
    <router-view />
  </div>
</template>

<style lang="scss" scoped>
.circle-3 {
  width: 10rem;
}
</style>

<script>
import CircleThree from '@/components/helpers/focus/CircleThree.vue';
import { apiGetOrder } from '@/assets/javascript/api';

export default {
  data() {
    return {
      defaultId: '',
      defaultCookie: '',
    };
  },
  props: ['cookieOrderList'],
  components: {
    CircleThree,
  },
  methods: {
    getOrder(id) {
      this.$emitter.emit('loadingStatus', true);
      apiGetOrder(id)
        .then((res) => {
          this.$emitter.emit('getOrderData', res.data.data);
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
  },
  mounted() {
    if (
      this.cookieOrderList.length > 0
      && this.$route.path.split('/')[this.$route.path.split('/').length - 1]
        === 'order'
    ) {
      this.defaultId = this.cookieOrderList[0].orderId;
      this.defaultCookie = this.cookieOrderList[0].cookieId;
      this.$router.push(`/order/${this.defaultCookie}`);
    } else if (this.cookieOrderList.length > 0) {
      this.cookieOrderList.forEach((item) => {
        if (item.cookieId.toString() === this.$route.path.split('/')[2]) {
          this.defaultId = item.orderId;
          this.defaultCookie = item.cookieId;
        }
      });
    }
    this.getOrder(this.defaultId);
  },
};
</script>
